import { configureStore } from '@reduxjs/toolkit';
import githubApi from 'src/api/githubApi'; //  API    GitHub
import reposDataReducer from './slices/reposData'; //     
import searchRepoRequestReducer from './slices/searchRepoRequest'; //       

//  Redux store   configureStore
export const store = configureStore({
	reducer: {
		//    API  
		[githubApi.reducerPath]: githubApi.reducer, //   RTK Query (API)
		reposData: reposDataReducer, //    
		searchRepoRequest: searchRepoRequestReducer, //      
	},
	middleware: (getDefaultMiddleware) => getDefaultMiddleware().concat(githubApi.middleware), //  middleware  RTK Query
});

//  store     
export default store;

//   RootState     Redux store
export type RootState = ReturnType<typeof store.getState>;

//   AppDispatch     Redux store
export type AppDispatch = typeof store.dispatch;
